import { createRouter, createWebHashHistory } from "vue-router";

const routes = [
  {
    path: '/',
    name: 'Index',
    component: () => import('@/views/index'),
    meta: {
      title: '',
      // keepAlive: true
    }
  },
  {
    path: '/record/power',
    name: 'RecordPower',
    component: () => import('@/views/record/power'),
    meta: {
      title: '算力記錄'
    }
  },
  {
    path: '/record/lpPower',
    name: 'RecordLpPower',
    component: () => import('@/views/record/lpPower'),
    meta: {
      title: 'LP质押記錄'
    }
  },
  {
    path: '/record/lpWithdraw',
    name: 'RecordLpWithdraw',
    component: () => import('@/views/record/lpWithdraw'),
    meta: {
      title: 'LP解押記錄'
    }
  },
  {
    path: '/record/mining',
    name: 'RecordMiningr',
    component: () => import('@/views/record/mining'),
    meta: {
      title: '挖矿记录'
    }
  },
  {
    path: '/record/lpMining',
    name: 'RecordLpMiningr',
    component: () => import('@/views/record/lpMining'),
    meta: {
      title: 'LP挖矿记录'
    }
  },
  {
    path: '/record/UploadToken',
    name: 'RecordUploadToken',
    component: () => import('@/views/record/uploadToken'),
    meta: {
      title: '上幣記錄'
    }
  },
  {
    path: '/token/upload',
    name: 'TokenUpload',
    component: () => import('@/views/token/upload'),
    meta: {
      title: '上幣'
    }
  },
  {
    path: '/join',
    name: 'Join',
    component: () => import('@/views/join'),
    meta: {
      title: '加入我們'
    }
  },
  {
    path: '/wallet',
    name: 'Wallet',
    component: () => import('@/views/wallet'),
    meta: {
      title: '钱包'
    }
  },
  {
    path: '/webview',
    name: 'Webview',
    component: () => import('@/views/webview'),
    meta: {
      title: ''
    }
  },
  {
    path: '/poolswap',
    name: 'Pool',
    component: () => import('@/views/pool'),
    meta: {
      title: ''
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
});

router.beforeEach((to) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
})

export default router;
